<template>
  <div class="container">
    <vue-headful
      :title="post.title + ' | Writing | Rhys Matthew | Web Developer'"
      :description="post.excerpt"
      :keywords="post.title + ', blog post by Rhys Matthew'"
      :head="{
        'meta[property=\'og:url\']': {content: 'https://rhys.digital/writing/' + this.$route.params.uid},
        'meta[name=\'twitter:site\']': {content: 'https://rhys.digital/writing/' + this.$route.params.uid},
        'link[rel=\'canonical\']': {href: 'https://rhys.digital/writing/' + this.$route.params.uid}
      }"
    />

    <PageHeader :postTitle="post.title"></PageHeader>

    <section class="section pt-2">
      <div class="post mw--800">
        <div class="section__block">
          <p class="post__date"><strong>Posted:</strong> {{ post.date }}</p>

          <prismic-rich-text :field="post.body"/>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
// Page header
import PageHeader from '../components/page-header/PageHeader'

export default {
  data: function () {
    return {
      post: {
        documentId: '',
        title: '',
        date: '',
        body: []
      }
    }
  },
  components: {
    PageHeader
  },
  created () {
    // Get post data
    this.getPostData(this.$route.params.uid)
  },
  mounted () {
    // eslint-disable-next-line
    setTimeout (() => {
      // Dispatch pageLoad event
      //
      const pageLoad = new CustomEvent('pageLoad')
      document.dispatchEvent(pageLoad)
    }, 250)
  },
  methods: {
    getPostData: function (uid) {
      // Query to get post content
      this.$prismic.client.getByUID('post', uid)
        .then((document) => {
          if (document) {
            this.post.documentId = document.id
            this.post.title = document.data.title
            this.post.date = this.reformatDate(document.data.posted_date)
            this.post.body = document.data.body
            this.post.excerpt = document.data.excerpt[0].text
          } else {
            // Return error page
            this.$router.push({ name: 'Page not found' })
          }
        })
    },
    reformatDate: function (postDate) {
      const dateOptions = { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' }
      const dateToFormat = new Date(postDate)

      return dateToFormat.toLocaleDateString('en-US', dateOptions)
    }
  }
}
</script>
